@tailwind base;
@tailwind components;
@tailwind utilities;

@font-face {
  font-family: 'mononoki';
  font-weight: 400;
  src: url('./mononoki-Regular Nerd Font Complete Mono.ttf');
}

@font-face {
  font-family: 'monofur';
  font-weight: 400;
  src: url('./monofur Nerd Font Complete Mono.ttf');
}

.arr {
  border: solid #dde9af;
  border-width: 0 20px 20px 0;
  display: inline-block;
  padding: 50px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
ul li button {
    color: #151515;
    text-decoration: none;
    letter-spacing: 0.15em;
    display: inline-block;
    padding: 15px 20px;
    position: relative;
}
ul li button:after {
  background: none repeat scroll 0 0 transparent;
  bottom: 0;
  content: "";
  display: block;
  height: 2px;
  left: 50%;
  position: absolute;
  background: #151515;
  transition: width 0.3s ease 0s, left 0.3s ease 0s;
  width: 0;
}
ul li button:hover:after {
  width: 100%;
  left: 0;
}
button:focus {
  outline: none;
  box-shadow: none;
}
